.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

// // ======================================================================
//    #Tablet styles
// // ======================================================================

@media (min-width: $tablet-width) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}

// // ======================================================================
//    #Desktop styles
// // ======================================================================

@media (min-width: $desktop-width) {
  .container {
    width: 1300px;
    padding: 0 40px;
  }
}
