.additional-goods__wrapper {
  display: flex;
  flex-direction: column;
}

.additional-goods__title {
  position: relative;

  display: inline-block;

  margin-top: 0;
  margin-bottom: 33px;
  margin-left: -20px;
  padding-right: 24px;
  padding-left: 20px;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 54%;
    z-index: -1;

    width: 100vw;
    height: 3px;

    background-color: $green;
  }

  &::before {
    right: 100%;
  }

  &::after {
    left: 89%;
  }
}

.additional-goods__list {
  @include list-base-style-reset;

  margin-bottom: 40px;
}

.additional-goods__item {
  padding-top: 13px;
  padding-bottom: 16px;

  border-top: 1px solid $line-gray;
}

.additional-goods__item:last-child {
  border-bottom: 1px solid $line-gray;
}

.additional-goods__item-wrapper {
  display: flex;
  flex-direction: column;

  margin-bottom: 14px;
}

.additional-goods__subtitle {
  width: 280px;
  margin: 0;
  margin-bottom: 13px;
}

.additional-goods__description {
  display: flex;
  justify-content: space-between;

  width: 280px;
  margin: 0;

  font-family: $second-font;
  line-height: 14px;
  color: $font-third-color;
  text-transform: none;
}

.additional-goods__button {
  width: 280px;
  padding: 11px 10px;
}

.additional-goods__gift {
  position: relative;

  margin: 0;
  margin-bottom: 34px;
  padding-top: 187px;
  padding-right: 59px;
  padding-bottom: 43px;
  padding-left: 59px;

  font-family: $second-font;
  font-size: 16px;
  line-height: 20px;
  color: $font-color-white;
  text-align: center;
  text-transform: none;

  background-color: $green-dark;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  &::before {
    content: "";

    position: absolute;
    top: 65px;
    left: 113px;

    width: 56px;
    height: 61px;

    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
  }
}

// // ======================================================================
//    ##Tablet styles
// // ======================================================================

@media (min-width: $tablet-width) {
  .additional-goods__title {
    margin-left: 0;
    margin-bottom: 80px;
    padding-right: 39px;
    padding-left: 37px;

    &::before {
      top: 60%;
    }

    &::after {
      top: 60%;
      left: 58%;
    }
  }

  .additional-goods__list {
    margin-bottom: 72px;
  }

  .additional-goods__item {
    padding-top: 25px;
    padding-right: 36px;
    padding-bottom: 25px;
    padding-left: 37px;

    border-right: 1px solid $line-gray;
    border-left: 1px solid $line-gray;
  }

  .additional-goods__item-wrapper {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: baseline;

    margin-bottom: 23px;
  }

  .additional-goods__subtitle {
    margin-bottom: 0;
  }

  .additional-goods__description {
    width: 235px;
    margin-left: auto;
    margin-bottom: 0;

    font-size: 16px;
    line-height: 20px;
  }

  .additional-goods__capacity {
    width: 145px;

    text-align: right;
  }

  .additional-goods__cost {
    width: 100px;

    text-align: right;
  }

  .additional-goods__button {
    width: 633px;
    padding-top: 11px;
    padding-right: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
  }

  .additional-goods__gift {
    margin-bottom: 76px;
    padding-top: 72px;
    padding-right: 110px;
    padding-bottom: 68px;
    padding-left: 423px;

    text-align: left;

    background-image: url("../img/gift-photo-tablet@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }

    &::before {
      top: 65px;
      left: 90px;
    }
  }
}

// // ======================================================================
//    ##Desktop styles
// // ======================================================================

@media (min-width: $desktop-width) {
  .additional-goods {
    margin-bottom: 90px;
  }

  .additional-goods__wrapper {
    flex-direction: row;
  }

  .additional-goods__title {
    margin-bottom: 80px;
    margin-left: -40px;

    &::after {
      left: 100%;
    }
  }

  .additional-goods__list {
    width: 895px;
    margin-bottom: 0;
  }

  .additional-goods__item {
    display: flex;

    padding-top: 16px;
    padding-right: 0;
    padding-bottom: 14px;
    padding-left: 0;

    border-left: none;
    border-right: none;
  }

  .additional-goods__item-wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    width: 610px;
    margin-bottom: 0;
  }

  .additional-goods__subtitle {
    width: 285px;
  }

  .additional-goods__description {
    width: 285px;
    margin-left: 0;
  }

  .additional-goods__capacity {
    width: 245px;

    text-align: left;
  }

  .additional-goods__cost {
    width: 140px;
  }

  .additional-goods__button {
    width: 245px;

    margin-left: auto;
  }

  .additional-goods__gift {
    width: 245px;
    margin-left: auto;
    margin-bottom: 0;
    padding-top: 187px;
    padding-right: 40px;
    padding-bottom: 0;
    padding-left: 40px;

    text-align: center;

    background-image: url("../img/gift-photo-desktop@1x.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }

    &::before {
      left: 95px;
    }
  }
}
