.main-nav--close {
  display: none;
}

.main-nav--no-js {
  display: block;
}

.main-nav__list {
  @include list-base-style-reset;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  text-align: center;
}

.main-nav__item {
  padding-top: 23px;
  padding-bottom: 20px;

  border-bottom: 1px solid $line-gray;
}

.main-nav__link {
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  color: $default-font-color;
  text-align: center;

  &[href]:hover,
  &[href]:focus {
    color: rgba(17, 17, 17, 0.6);
  }

  &[href]:active {
    color: rgba(17, 17, 17, 0.3);
  }
}

.main-nav__link--current {
  position: relative;

  &::after {
    content: "";

    position: absolute;
    bottom: -5px;
    left: 0;

    width: 97%;
    height: 2px;
  }
}

.main-nav__link--current-green {
  &::after {
    background-color: $green;
  }
}

// // ======================================================================
//    ##Tablet styles
// // ======================================================================

@media (min-width: $tablet-width) {
  .main-nav {
    display: block;
  }

  .main-nav__wrapper {
    width: 440px;
  }

  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
  }

  .main-nav__item {
    padding-top: 23px;
    padding-bottom: 23px;

    border: none;
  }

  .main-nav__item:last-child {
    border: none;
  }

  .main-nav__link {
    font-size: 18px;
    line-height: 25px;
    font-weight: 400;
    color: $font-second-color;
  }

  .main-nav__link--current {
    position: relative;

    &::after {
      bottom: -9px;
    }
  }
}

// // ======================================================================
//    #Desktop styles
// // ======================================================================

@media (min-width: $desktop-width) {
  .main-nav {
    z-index: 3;
  }

  .main-nav__wrapper {
    width: 480px;
  }

  .main-nav__item {
    padding: 26px 0;
  }

  .main-nav__link {
    font-size: 20px;
    line-height: 25px;
    font-weight: 400;
    color: $font-color-white;

    &[href]:hover,
    &[href]:focus {
      color: rgba(255, 255, 255, 0.6);
    }

    &[href]:active {
      color: rgba(255, 255, 255, 0.3);
    }
  }

  .main-nav__link--black {
    color: $default-font-color;

    &[href]:hover,
    &[href]:focus {
      color: rgba(17, 17, 17, 0.6);
    }

    &[href]:active {
      color: rgba(17, 17, 17, 0.3);
    }
  }

  .main-nav__link--current-white {
    &::after {
      background-color: $bg-color-white;
    }
  }
}
