.title-h2 {
  margin: 0;
  padding: 0;

  font-weight: 400;
  color: $default-font-color;
  text-transform: none;
}

.title-h2--size-xs {
  font-size: 24px;
  line-height: 24px;
}

.title-h2--size-m {
  font-size: 36px;
  line-height: 60px;
}

// // ======================================================================
//    ##Tablet styles
// // ======================================================================

@media (min-width: $tablet-width) {
  .title-h2--size-s {
    font-size: 36px;
    line-height: 36px;
  }

  .title-h2--size-l {
    font-size: 60px;
    line-height: 60px;
  }
}
